@mixin sticky {
  position: sticky;
  top: 0;
}
.menu,
.content,
.submenu {
  border-right: 3px solid lightcoral;
  transition: all .5s
}
.menu,
.submenu {
  height: 100vh;
}
.content {
  overflow-y: auto;
  background-color: rgba(221, 183, 86, 0.671);
}
div.menu{
    @include sticky();
    background-color: rgba(73, 63, 63, 0.486);
}
div.submenu {
  @include sticky();
  background-color: rgba(85, 85, 170, 0.568);
}

.menus {
    font-size: 5vw;
}

.todo-item {
    width: 100px;
    height: 100px;
    background-color: red;
    float: left;
    border: solid 2px white;
    box-sizing: border-box;
  }
  
  .example-enter {
    opacity: 0.01;
    width: 0px;
  }
  
  .example-enter.example-enter-active {
    opacity: 1;
    width: 100px;
    transition: 700ms;
  }
  
  .example-leave {
    opacity: 1;
    width: 100px;
  }
  
  .example-leave.example-leave-active {
    opacity: 0.01;
    width: 0px;
    transition: 700ms;
  }
  .mobile-list {
    font-size: 35px;
    line-height: 50px;
    &:active {
      color: beige;
    }
  }
  .desktop-list {
    font-size: 40px;
    &:hover {
      color: beige;
    }
  }
@import "./content.scss";
@import './menu.scss';
